.container {
  max-width: $l-end;
  min-width: $small;
  margin: 0 auto;
  padding-left: 6.7%;
  padding-right: 6.7%;

  @media (max-width: $m-end) {
    width: 100%;
    padding: 0 21px;
  }

  @media (max-width: $s-end) {
    padding: 0 15px;
  }

  &--footer {
    @media (max-width: $s-end) {
      padding: 0;
    }
  }
}
