*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  display: block;
}

input,
textarea,
select,
button {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

a:not([href]) {
  cursor: auto;
}

button {
  cursor: pointer;
}

svg {
  fill: currentColor;
}
